<template>
<div>
  
<iframe ref="iframe" scrolling="auto" width="100%" height="100%" frameborder="0"></iframe>
</div>
  
</template>
<script>
export default {
  name: "task",
  data() {
    return {};
  },
  methods: {
    getUrl() {
      this.$refs.iframe.contentWindow.location.replace(
        "https://voice.baidu.com/act/virussearch/virussearch?from=osari_map&tab=0&infomore=1"
      );
    }
  },
  mounted() {
    this.getUrl();
    document.body.style.backgroundColor = "#FFFFFF";
  }
};
</script>

<style>
iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  border: 0;
}
</style>